.formMailContainer {
	padding: 0px 15vw 0px;
	width: 100%;
	height: 500px;
	margin-bottom: 120px;
	display: flex;

	.formMailTitWrap {
		flex: 3;
		height: 100%;
		background: radial-gradient(circle, #f40000, #ff8a00 14.06%, #f9ad05 37.76%, #fc348c 62.77%, #b258cb 78.91%, #00c4b4);
		position: relative;
		border-radius: 10px 0px 0px 10px;
		background-size: 200%;
		animation: animation 1.7s linear infinite;
		margin-bottom: 15px;
		cursor: pointer;

		@keyframes animation {
			to {
				background-position: 200% center;
			}
		}

		.formMailTitWrapW {
			width: 99%;
			height: 99%;
			margin: 0.5%;
			background: #fff;
			position: absolute;
			z-index: 2;
			border-radius: 10px;
			display: flex;
			justify-content: center;
			flex-direction: column;
			text-align: center;
			align-items: center;
			border-radius: 10px 0px 0px 10px;

			.fa-mobile {
				font: 35px/1 $ikeaFontKo;
				color: $fontColorBl;
				margin-bottom: 15px;
			}

			h2 {
				font: 28px/1 $ikeaFontKo;
				color: $fontColorBl;
			}
		}
	}

	.formMailWrap {
		flex: 7;
		height: 100%;
		border-radius: 0px 10px 10px 0px;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		padding: 30px;
		background: $lightGrey;

		form {
			width: 100%;

			.formTopWrap {
				width: 100%;
				height: calc(100% / 4);
				display: flex;
				padding: 15px 0px 45px 0px;
				justify-content: space-between;
				gap: 15px;

				input[type='text'] {
					width: 100%;
					background: #fff;
					border-radius: 5px;
					padding: 20px;
					font: 15px/1 $ikeaFontKo;
					color: $fontColorBl;
					border: none;

					&:focus {
						outline: none;
					}

					&::placeholder {
						font: 15px/1 $ikeaFontKo;
						color: $fontColorGrC;
					}
				}
			}

			.formMiddleWrap {
				width: 100%;
				height: calc(100% / 2);
				display: flex;
				justify-content: center;

				textarea {
					width: 100%;
					border: none;
					border-radius: 5px;
					padding: 20px;
					font: 15px/1 $ikeaFontKo;
					color: $fontColorBl;

					&:focus {
						outline: none;
					}

					&::placeholder {
						font: 15px/1 $ikeaFontKo;
						color: $fontColorGrC;
					}
				}
			}

			.formBottomWrap {
				width: 100%;
				height: calc(100% / 6);
				display: flex;
				justify-content: end;
				padding: 30px;
				gap: 30px;

				p {
					font: 15px/3 $ikeaFontKo;
					color: $hoverBtn;
				}

				.submitBg {
					width: 155px;
					height: 50px;
					background: radial-gradient(circle, #f40000, #ff8a00 14.06%, #f9ad05 37.76%, #fc348c 62.77%, #b258cb 78.91%, #00c4b4);
					position: relative;
					border-radius: 10px;
					background-size: 200%;
					animation: animation 1.7s linear infinite;
					margin-bottom: 15px;
					cursor: pointer;
					padding: 1px;

					@keyframes animation {
						to {
							background-position: 200% center;
						}
					}

					.submitBtn {
						width: 152px;
						height: 47px;
						border-radius: 10px;
						font: 14px/1 $ikeaFontKo;
						color: $fontColorBl;
						background: #fff;
						position: relative;
						margin: 0px auto;
						border: none;
						background-size: 200%;
						animation: animation 1.7s linear infinite;
						margin-bottom: 15px;
						cursor: pointer;
					}
				}
			}
		}
	}
}

