							font: 11.2px/1 $ikeaFontKo;
							color: $fontColorBl;
						}
					}

					.rightWrap {
						display: flex;
						gap: 5px;
						font: 11.2px/1 $ikeaFontKo;
						color: $fontColorGrC;

						button {
							width: 84px;
							height: 32px;
							border-radius: 5px;
							background: $tableBtn;
							font: 11.2px/1 $ikeaFontKo;
							color: $fontColorW;
						}

						.fa-heart {
							transform: translateY(3px);
							cursor: pointer;
						}
					}
				}
			}
		}
	}
}
